  <template>
  <transition name="main__product-cards">
    <div class="main__product-cards" v-show="showOrNoShow">
      <div
        class="main__product-card"
        v-for="card in getItemCards"
        :key="card.id"
      >
        <img class="main__product-img" :src="card.img" alt="" />
        <RouterLink
          class="main__product-title"
          :to="'/home/' + card.id"
        >
          <div class="product__img-hover">
            <div class="product__img-hover_flex">
              <p class="product__img-hover_text">Смотреть рецепт</p>
              <font-awesome-icon icon="fa-solid fa-arrow-right" />
            </div>
          </div>
        </RouterLink>
        <h3 class="main__product-title">{{ card.title }}</h3>
        <div class="main__product-clockflex">
          <button class="main__product-clock">
            <font-awesome-icon icon="fa-solid fa-clock" class="black__3" /> 15
            минут
          </button>
          <div class="main__product-rightStar">
            <font-awesome-icon icon="fa-solid fa-star" />
            <font-awesome-icon icon="fa-solid fa-star" />
            <font-awesome-icon icon="fa-solid fa-star" />
            <font-awesome-icon icon="fa-solid fa-star" />
            <font-awesome-icon icon="fa-solid fa-star" />
            <p class="rightStar__number">4.2</p>
          </div>
        </div>
        <div class="main__product-lastFlex">
          <div class="main__pr-item">
            <img src="@/assets/img/product1.png" alt="" />
          </div>
          <div class="main__pr-item">
            <img src="@/assets/img/product2.png" alt="" />
          </div>
          <div class="main__pr-item">
            <img src="@/assets/img/product3.png" alt="" />
          </div>
          <button class="main__pr-lastBtn">Купить ингредиенты</button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import db from "@/db";

export default {
  props: {
    showOrNoShow: Boolean,
    filter1: Boolean,
    filter2: Boolean,
  },
  data() {
    return {
      cards: db,
    };
  },
  computed: {
    getItemCards() {
      if (this.filter1) {
        return this.cards.filter((card) => card.title == "Курочка BARBEQUE");
      }
      if (this.filter2) {
        return this.cards.filter((card) => card.title !== "Курочка BARBEQUE");
      }
      if (this.filter1 || this.filter2) {
        return this.cards.filter(
          (card) => card.title == "Курочка BARBEQUE" || "Рождественский сюрприз"
        );
      } else {
        return this.cards.filter(
          (card) => card.title == "Курочка BARBEQUE" || "Рождественский сюрприз"
        );
      }
    },
  },
};
</script>

<style></style>
